<template>
	<view>
		<!-- 头部组件 tabtap接收子组件传的当前点亮的组件下标-->
		<swiperTabHead class="swiperTabHead" :tabBars="tabBars" :tabIndex="tabIndex" @tabtap="tabtap"></swiperTabHead>
		<!-- 下面的内容区域 -->
		<!-- <view class="uni-tab-bar"> -->
			<!-- 用uniapp自带的轮播组件包裹 -->
		    <swiper class="swiper-box" :style="{height:swiperheight+'rpx'}" :current="tabIndex" @change="tabChange">
				<!-- 每一个轮播里面的内容 -->
		        <swiper-item v-for="(items,index) in newslist" :key="index">
					<!-- y轴滑动 -->
					<view class="Receiving_null" v-if="items.list.lenght==0">
						<image src="../../static/image/Receiving_address.png"></image>
					</view>
		            <scroll-view v-else scroll-y class="list">
		                <template>
		                    <!-- 图文列表 -->
		                    <!-- 现在渲染每一个订单状态的组件 -->
							<order-module class="Module" @SonRemove="SonRemove" @SonCancel="SonCancel" v-for="item in items.list" :key="item.id" :item="item" ></order-module>
		                </template>
		            </scroll-view>
		        </swiper-item>
		    </swiper>
		<!-- </view> -->
	</view>
</template>
<script>
	import swiperTabHead from "../swiper-tab-head.vue";
	// 引入渲染每个订单内容组件
	import OrderModule from '../OrderModule.vue';
	export default {
		data() {
			return {
				tabIndex:0,// 选中的
				tabBars:[
					{ name:"全部订单",id:1},
					{ name:"待付款",id:2},
					{ name:"待收货",id:3},
					{ name:"待评价",id:4}
				],
				// 动态获取页面高度
				swiperheight:0,
				// 每个页面的数据
				newslist:[
					{id:0,list:[]},
					{id:1,list:[]},
					{id:2,list:[]},
					{id:3,list:[]}
				],
				// 定义state的值
				state:0
			}
		},
		// 引入头部组件
		components:{
		    swiperTabHead,
			OrderModule
		},
		onLoad(options) {
			// 获取上一个页面传过来的state值
			this.state=options.state
			// 动态根据“我的”页面传过来的state值，显示不同的“订单状态”页面
			// 进来就加载
			this.Showorder(options.state)
			
			// 页面切换动画的效果实现
			uni.getSystemInfo({
			    success:(res)=>{
			        let height = res.windowHeight-uni.upx2px(100)
			        this.swiperheight = height;
			    }
			})
		},
		methods: {
			// 发起请求
			getOrder(State){
				// 给相应的数组组件相应的数据
				this.newslist.forEach(async item=>{
					// 判断是哪个订单状态
					if(item.id==State){
						// 赋值,增加判断，减少资源浪费,不用切换一次就发起一次请求
						if(item.list.length<1){
							const res=await this.$myRequest({
								url:'/order',
								header:uni.getStorageSync("token"),
								data:{state:State,page:1}
							})
							// console.log(res)
							item.list=res.data.data.data
							// console.log(item.list)
							
							// 动态把页面长度获取传值
							this.swiperheight=item.list.length*435
						}
					}
				})
			},
			// 点击导航栏切换组件内容
			tabtap(index){
			    this.tabIndex = index;
				// 点击不同的导航“订单状态”，获得不同的内容
				// 同样发起请求，获取数据
				this.getOrder(index)
				// console.log(index)
				// console.log(this.newslist[index])
			},		
			//滑动切换导航
			tabChange(e){
				this.tabIndex = e.detail.current;
				// 滑动页发起请求，请求数据
				this.getOrder(e.detail.current)
			},
			// 封装
			PostApi(el,Api){
				// 弹出确定取消的消息框
				uni.showModal({
				    content: '亲，确认取消订单吗',
				    success:async (res)=>{
					   	if (res.confirm) {
							// 点击确定就发起清空的请求
							const clear=await this.$myRequest({
								method:"POST",
								header:uni.getStorageSync("token"),
								url:Api,
								// 请求的订单号--el
								data:{order_sn:el}
							})
							// 更新列表
							this.getOrder(this.state)
						} else if (res.cancel) {
							return
						}     
					}
				});
			},
			// 父级接收取消订单的点击事件
			SonCancel(el){
				this.PostApi(el,"/order/cancel")
			},
			// 删除订单的点击事件
			SonRemove(el){
				this.PostApi(el,"/order/del")
			},
			// 决定刚开始进来，显示哪个订单内容
			Showorder(state){
				// console.log(state)
				// 知道结果的值的判断用switch优化
				switch(state){
					// 将当前锁定到的页面，显示到相应的tabIndex
					case '0':
					this.tabIndex=0;
					this.getOrder(state); 
					break;
					
					case '1':
					this.tabIndex=1;
					this.getOrder(state);
					break;
					
					case '2':
					this.tabIndex=2;
					this.getOrder(state);
					break;
					 
					case '3':
					this.tabIndex=3;
					this.getOrder(state);
					 break;
				}
			}
		}
	}
</script>

<style lang="less" scoped>
	.swiperTabHead{
		/* 让导航栏悬浮 */
		position: fixed;
		top: 70rpx;
		left: 0rpx;
		width: 100%;
		height: 75rpx;
		z-index: 99;
	}
	.Module:nth-of-type(1){
		padding-top:80rpx;
	}
	.Receiving_null{
		width: 100%;
		height: 700rpx;
		position: fixed;
		image{
			width:180rpx;
			height:180rpx;
			position: absolute;
			left: 50%;
			bottom: 0rpx;
			transform: translateX(-50%);
		}
	}
</style>
